}
.item-list .list-group-item {
    cursor: pointer;
}
.char-details {
    background-color: #fff;
    padding: 25px 25px 15px 25px;
    margin-bottom: 40px;
}

.char-details h4 {
    margin-bottom: 20px;
    text-align: center;
}

.select-error {
    color: #fff;
    text-align: center;
    font-size: 26px;
}
.headerBlock {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;
}

.headerTitle{
    font-size: 24px;
    color: #fff;
    margin: 0;
}

.headerLinks{
    display: flex;
    margin: 0;
    align-items: center;
    color: #fff;
    list-style-type: none;
}

.headerLinks li {
    margin-right: 20px;
    font-size: 18px;
}


.closeRandomChar{
    margin-bottom: 20px;
    border: none;
    background-color: white;
    border-radius: 5px;
    padding: 5px;
}
